export default "/* Style The Dropdown Button */\n.dropbtn {\n  background-color: transparent;\n  color: #808080;\n  border: 1px solid transparent;\n  cursor: pointer;\n  font-family: sans-serif;\n  font-weight: 550;\n  font-size: 12px;\n  line-height: 15px;\n  outline: none;\n  text-align: center;\n  width: 100%;\n  height: 21px;\n  margin-top: -1px;\n  padding-right: 10px;\n  border-bottom: 2px solid transparent;\n}\n.dropbtn::after {\n  content: \"\";\n  width: 0;\n  height: 0;\n  border-left: 4px solid transparent;\n  border-right: 4px solid transparent;\n  border-top: 4px solid #505050;\n  border-bottom: none;\n  position: absolute;\n  top: 7px;\n  right: 6px;\n}\n/* The container <div> - needed to position the dropdown content */\n.dropdown {\n  position: absolute;\n  display: inline-block;\n  left: 0px;\n  width: 100%;\n}\n/* Dropdown Content (Hidden by Default) */\n.dropdown-content {\n  display: none;\n  position: absolute;\n  background-color: #ececec;\n  min-width: 85px;\n  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n  z-index: 500;\n  top: 19px;\n}\n/* Links inside the dropdown */\n.dropdown-content div {\n  color: black;\n  padding: 6px 8px;\n  text-decoration: none;\n}\n/* Change color of dropdown links on hover */\n.dropdown-content div:hover {\n  background-color: #fefefe;\n}\n/* Show the dropdown menu on hover */\n/* Change the background color of the dropdown button when the dropdown content is shown */\n.dropdown:hover .dropbtn {\n  color: #00006d;\n}\n.nav-tabs li.active .dropbtn {\n  color: #00006d;\n  background-color: whitesmoke;\n  border: 1px solid #B3B3B3;\n  border-bottom: 2px solid whitesmoke;\n}\n.nav-tabs li.open .dropbtn::after {\n  content: \"\";\n  border-top: none;\n  border-bottom: 4px solid #505050;\n}\n#topology_dropdown, #object_dropdown {\n  display: none;\n  position: absolute;\n  width: -webkit-fit-content;\n  width: -moz-fit-content;\n  width: fit-content;\n  transition: display 0.4s;\n  z-index: 99999999;\n  background-color: #fafafb;\n  border: 1px solid #808080;\n  box-shadow: none;\n}\n#topology_dropdown div, #object_dropdown div {\n  cursor: pointer;\n  display: block;\n  min-width: 100px;\n  max-width: 300px;\n  overflow: hidden;\n  line-height: 20px;\n  background-color: #fafafb;\n  border: 0px;\n  margin: 0px;\n  padding: 0px 10px 0px 10px;\n  line-height: 30px;\n  height: 30px;\n  font-size: 12px;\n  color: #373737;\n}\n#topology_dropdown div:hover, #object_dropdown div:hover {\n  color: #00006d;\n  background-color: #E6E6E6;\n}\n/*# sourceMappingURL=data:application/json;base64, */"